import React from 'react';

import {Avatar, Cell, Div, Group, Header, Panel, Text} from "@vkontakte/vkui";

const UserText = ({go, fetchedUser,id}) => {
    return (
        <Panel id={id}>

            {fetchedUser &&
                <Group mode={"card"} className="blok" >
                    <Cell
                        before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
                        description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}

                    >
                        {`${fetchedUser.first_name} ${fetchedUser.last_name} `}
                    </Cell>
                </Group>}


        <Group className="cart" mode={"card"} header={<Header mode="secondary">Информация о вузе(Новости)</Header>}>
            <Div>
                <Text>
                    Подготовка конкурентоспособных специалистов высокого уровня компетентности с учетом требований модернизации экономики работодателя, способных работать в экономических системах, основанных на знаниях, активно влиять на инновационное социально- экономическое, духовное развитие Ивановского края, России.

                </Text>
            </Div>
        </Group>

    <Group className="raz" mode={"card"}  header={<Header mode="secondary">Версия 2.1.1</Header>}>
        <Div>
            <Text>
                • Добавлен новый дизайн
            </Text>
            <Text>
                • Исправлены ошибки в совместимости
            </Text>
            <Text>
                • Исправлены мелкие недочёты
            </Text>
        </Div>
    </Group>
        </Panel>
    );
};

export default UserText;