import React from 'react';
import {Button, Div, Group} from "@vkontakte/vkui";
import {Icon28ChevronRightOutline, Icon28UserOutline} from "@vkontakte/icons";
import {NavLink} from "react-router-dom";

const Buttons = ({go}) => {
    return (
        <Group className="Persik" mode={"card"} >
            <Div>
                <NavLink to ='/prof'><Button className="buton" size="l" mode="secondary" expandable before={<Icon28UserOutline />}>Профиль</Button></NavLink>
                <NavLink to='/Curs1'><Button className="buton" size="l" mode="secondary" expandable before={<Icon28ChevronRightOutline />} >1 Курс</Button></NavLink>
                <Button size="l" mode="secondary" expandable before={<Icon28ChevronRightOutline />}   className="buton" >2 Курс</Button>
                <Button size="l" mode="secondary" expandable before={<Icon28ChevronRightOutline />}   className="buton" >3 Курс</Button>
                <Button size="l" mode="secondary" expandable before={<Icon28ChevronRightOutline />}   className="buton" >4 Курс</Button>
                <Button size="l" mode="secondary" expandable before={<Icon28ChevronRightOutline />}   href="" className="buton" >5 Курс</Button>
            </Div>
        </Group>
    );
};

export default Buttons;